<template>
 <div class="BigBox">
   <shop-car-header></shop-car-header>
   <shop-List :shopCarData="shopCarData" :shopCheckAll="shopCheckAll"></shop-List>
   <shop-car-footer :CheckAll="CheckAll" :zj="proChecked" :shopCarData="shopCarData"></shop-car-footer>
 </div>
</template>

<script>
import shopCarHeader from"../components/shopCar/shopCarHeader"
import shopList from "../components/shopCar/shopList"
import shopCarFooter from "../components/shopCar/shopCarFooter"
 export default {
   data () {
     return {
       name:'shopCar',
       shopCarData:[
         {
            totalPrice: 0,
            allchecked: false,
           "shop":[
              {
              "shopname":"",
               "checked":false,
              "shopTotalPrice":0,
              "product":[
                {
                  "Img":"static/img/car1.jpg",
                  "Info":"",
                  "color":"",
                  "size":"M",
                  "price":"69.00",
                  "yprice":"98.58",
                  "num":"1",
                  "check":false
                },
                {
                  "Img":"static/img/a3.jpg",
                  "Info":"",
                  "color":"",
                  "size":"s",
                  "price":"136.00",
                  "yprice":"254",
                  "num":"1",
                  "check":false
                },
              ]
            },
            {
              "shopname":"Mini",
               "checked":false,
                "shopTotalPrice":0,
              "product":[
                {
                  "Img":"static/img/car2.jpg",
                  "Info":"2019++",
                  "color":"",
                  "size":"M",
                  "price":"145.00",
                  "yprice":"247.00",
                  "num":"1",
                  "check":false
                }
              ]
            },
            // {
            //   "shopname":"",
            //   "product":[
            //     {
            //       "Img":"/static/img/gl3.jpg",
            //       "Info":"2019++",
            //       "color":"",
            //       "size":"M",
            //       "price":"145.00",
            //       "yprice":"247.00",
            //       "num":"1"
            //     },
            //     {
            //       "Img":"/static/img/gl1.jpg",
            //       "Info":"2019++",
            //       "color":"",
            //       "size":"M",
            //       "price":"145.00",
            //       "yprice":"247.00",
            //       "num":"1"
            //     }
            //   ]
            // }
           ]
         }
       ]
     }
   },
   components: {
     shopCarHeader,
     shopList,
     shopCarFooter
   },
    methods:{
      /**
       * 
       * */
      CheckAll(){
        let isCheck = this.shopCarData[0].allchecked;
        let shops = this.shopCarData[0].shop;//
        for(let i=0;i<shops.length;i++){
          let poducts =shops[i].product;//
          for(let j=0;j<poducts.length;j++){
            if(isCheck){
              poducts[j].check = true;//
              shops[i].checked = true;//
            }else{
              poducts[j].check = false;//
              shops[i].checked = false;//
            }
          }
        }
      },
      /**
       * 
       * */
      shopCheckAll(i){  //icartContent
        let isCheck = this.shopCarData[0].shop[i].checked;
        this.shopCarData[0].shop[i].product.forEach((product,index)=>{
          product.check=isCheck;
        })
      },
      // addSum(sid,pid){
      //   this.shopCartData[0].shop[sid].product[pid].num++
      // },
      // reduce(sid,pid){
      //   if(this.shopCartData[0].shop[sid].product[pid].num<=1){
      //     this.shopCartData[0].shop[sid].product[pid].num=1
      //   }else{
      //     this.shopCartData[0].shop[sid].product[pid].num--
      //   }
      // },
    },
    computed: {
      /**
       * 
       * @param shopCartData
       */
      proChecked(data){
        let total = 0;
        let SumNum = 0;
        this.shopCarData[0].shop.forEach((shopId,i)=>{
          //   pro.checked=this.allchecked
          shopId.product.forEach((product, i) => {
              // products.checked = this.allchecked
             if(product.check){
               total += product.price * product.num;
               SumNum += product.num;
             }
            })
        })
        // console.log(SumNum)  //
        return this.shopCarData[0].totalPrice = total.toFixed(2)
      },




    },
 }
</script>

<style>
@import "../assets/css/index.css";
 .BigBox{
    width:100%;
    /* height:100%; */
    height:100%;
    display:flex;
    flex-direction: column;
  }
</style>



